import { Link, Outlet } from "@remix-run/react";
import { useState } from "react";

export default function AdminLayout() {
    const [open, setOpen] = useState(true);

    return (
        <div className="min-h-screen flex bg-gray-100">
            {/* Sidebar */}
            <aside
                className={`bg-gray-800 text-white transition-all duration-300 ${open ? "w-64" : "w-16"
                    }`}
            >
                <button
                    onClick={() => setOpen(!open)}
                    className="p-4 text-white focus:outline-none"
                >
                    {open ? "←" : "→"}
                </button>

                <nav className="p-2 space-y-2">
                    <Link to="/admin/dashboard" className="block px-3 py-2 hover:bg-gray-700">
                        🏠 {open && "Dashboard"}
                    </Link>
                    <Link to="/admin/users" className="block px-3 py-2 hover:bg-gray-700">
                        👥 {open && "Users"}
                    </Link>
                </nav>
            </aside>

            {/* Content */}
            <main className="flex-1 p-6 overflow-y-auto">
                <Outlet />
            </main>
        </div>
    );
}
